import React from 'react'
import Container from '../../components/Container'
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from 'chart.js';
import { Radar } from 'react-chartjs-2'
import * as s from './styles'
import { splitString } from '../../utils/splitString'

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

const labels = [
  [' ', ' '],
  [' ', ' '],
  [' ', ''],
  ['  ', ''],
  [' '],
  ['  ', ' ', ''],
  [' ', ' ', '  ']
]

export const Cabinet = () => {
  const data = {
    labels,
    datasets: [
      {
        label: '%  ',
        data: [0, 0, 71, 0, 0, 0, 0],
        backgroundColor: 'transparent',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 6,
      }
    ]
  }

  const options = {
    responsive: true,
    tooltip: {
      enabled: true,
    },
    layout: {
      beginAtZero: true,
      padding: {
        right: 35,
        bottom: 60
      }
    },
    scales: {
      r: {
        min: 0,
        max: 100,
        gridLines: {
          circular: false
        },
        ticks: {
          callback: (value) => {
            return `${value}%`;
          },
        },
        pointLabels: {
          maxWidth: 100,
          padding: 10,
          font: {
            size: 14
          },
          callback(value) {
            return value
          }
        }
      }
    },
  };

  return (
    <Container>
      <s.Table>
        <s.Thead>
          <tr>
            <s.Td colSpan="2">  </s.Td>
          </tr>
        </s.Thead>
        <tbody>
        <tr>
          <s.Td> :</s.Td>
          <s.Td>:</s.Td>
        </tr>
        <tr>
          <s.Td>   </s.Td>
          <s.Td>0</s.Td>
        </tr>
        <tr>
          <s.Td>   </s.Td>
          <s.Td>0</s.Td>
        </tr>
        <tr>
          <s.Td>  </s.Td>
          <s.Td>71%</s.Td>
        </tr>
        <tr>
          <s.Td>   </s.Td>
          <s.Td>0</s.Td>
        </tr>
        <tr>
          <s.Td> </s.Td>
          <s.Td>0</s.Td>
        </tr>
        <tr>
          <s.Td>     </s.Td>
          <s.Td>0</s.Td>
        </tr>
        <tr>
          <s.Td>      </s.Td>
          <s.Td>0</s.Td>
        </tr>
        </tbody>
      </s.Table>
      <s.ChartWrapper>
        <Radar type='radar' data={data} options={options} />
      </s.ChartWrapper>
    </Container>
  )
}
